* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
  text-decoration: none;
}

html {

  background: rgba(8, 71, 98, 0.774); 
     background-image: url(https://s3.postimg.org/s1n3ji1ur/paper_fibers_2_X.png);
  box-sizing: border-box;
  padding: 0px 8px 15px 8px ;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.subtle {
  color: #aaa;
}

.main-card--cointainer {
  display: grid;
  grid-template-columns: repeat(3, minmax(300px, 600px));
  grid-gap: 50px;
  justify-content: space-around;
  
}

.card-container {
  
  margin: 25px auto 0;
  position: relative;
  display: flex;
flex-direction: row;
   justify-content: center;

  /* align-items: flex-start; */
  /* border: 2px solid blue; */
}

.card-container img {
margin-top: 10px;    
width: 65%;
  height: 70%;
}

.card {
  background-color: #ffffff;
  padding: 8px 7px 6px 7px ;
  position: relative;
  box-shadow: 0 0 5px rgba(75, 75, 75, 0.07);
  z-index: 1;
  height: 500px;
  width: 374.67px;
  border: 3px solid transparent;
  border-radius: 10px;
}

.card-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
}
.card {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
}

/* .card-number {
  margin-top: 15px;
} */

.card-circle {
  border: 1px solid #aaa;
  border-radius: 50%;
  display: inline-block;
  line-height: 22px;
  font-size: 12px;
  height: 25px;
  text-align: center;
  width: 25px;
}

.card-author {
  display: block;
  font-size: 18px;
  text-align: right;
  letter-spacing: 0.5px;
  margin: 8px 0 12px;
  font-weight: 530;
  color: #6a6a6a;
}

.card-title {
   /* border: 1px solid red; */
   min-height: 60px;
  font-family: "Cormorant Garamond", serif;
  font-size: 32px;
  font-weight: 300;
  /* line-height: 30px; */
  margin: 8px 0px 0px 10px;
  text-transform: capitalize;
}

.card-description {
  /* display: inline-block; */
  font-weight: 300;
  line-height: 22px;
  margin: 10px 0;
}

.card-read {
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 6px;
  margin: 5px 0 20px;
  position: relative;
  text-align: right;
  text-transform: uppercase;
}

.card-read:after {
  background-color: #b8bddd;
  content: "";
  display: block;
  height: 1px;
  position: absolute;
  top: 9px;
  width: 75%;
}

.card-tag {
  float: right;
  margin: 5px 0 0;
  border: 1px solid #aaa;
  padding: 5px 10px;
  cursor: pointer;
}

.card-tag:hover {
  background: #aaa;
  color: black;
}

.card-media {
  max-width: 100%;
  height: auto;
}

.card-shadow {
  background-color: #fff;
  box-shadow: 0 2px 25px 2px rgba(0, 0, 0, 1), 0 2px 50px 2px rgba(0, 0, 0, 1),
    0 0 100px 3px rgba(0, 0, 0, 0.25);
  height: 1px;
  margin: -1px auto 0;
  width: 80%;
  z-index: -1;
}

@media (max-width: 998px) {
  .main-card--cointainer {
    display: grid;
    grid-template-columns: repeat(1, minmax(200px, 500px));
    grid-gap: 50px;
    justify-content: space-around;
  }
}

